#graphmilker {
  .gm-postDetails {
  }

  .gm-postDetails__header {
    display: flex;
  }

  .gm-postDetails__title {
    flex-grow: 1;
  }

  .gm-postDetails__postDate {
    margin-left: 10px;
  }

  .gm-postDetails__buttons {
    flex-grow: 0;
  }

  .gm-postDetails__buttons .gm-button {
    margin-left: 10px;
  }

  .gm-postDetails__body {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    margin: 0 -20px;
    padding: 0 20px;
  }
  .gm-postDetails__body a {
    color: #365899;
  }
  .gm-postDetails__imageFrame {
    margin: 0 -20px -20px;
  }

  .gm-postDetails__footer {
    font-size: 12px;
    text-align: right;
  }

  .gm-postDetails__link {
    font-size: 12px;
    color: #90949c;
  }
  .gm-postDetails__link:hover {
    color: #365899;
    text-decoration: underline;
  }
}
