#graphmilker {
  .gm-overlay {
    background-color: rgba(0, 0, 0, 0.6);
    bottom: 0;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 1000000;
  }

  .gm-overlay__inner {
    bottom: 0;
    left: 0;
    overflow: auto;
    position: absolute;
    right: 0;
    text-align: center;
    top: 0;
  }

  .gm-overlay__inner:before {
    content: '';
    display: inline-block;
    height: 100%;
    margin-right: -0.25rem;
    vertical-align: middle;
  }

  .gm-overlay__content {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3), 0px 4px 10px rgba(0, 0, 0, 0.3);
    display: inline-block;
    margin: 50px 0;
    max-width: 500px;
    position: relative;
    vertical-align: middle;
    width: 95%;
  }

  .gm-overlay__closeButton {
    appearance: none;
    border: none;
    background-color: rgba(255,255,255,0.3);
    position: absolute;
    right: 0;
    top: 0;
    border-radius: 0;
    cursor: pointer;
    height: 44px;
    width: 44px;
    font-size: 22px;
    line-height: 44px;
  }

  .gm-overlay__closeButton:hover,
  .gm-overlay__closeButton:focus {
    background-color: rgba(255,255,255,0.45);
    outline: none;
  }

  .gm-overlay__closeButton span {
    clip: rect(1px, 1px, 1px, 1px);
    position: absolute;
    height: 1px;
    width: 1px;
    overflow: hidden;
  }
}
