#graphmilker {
  *, *::before, *::after {
    box-sizing: border-box;
  }

  li {
    margin-bottom: 0;
  }

  .gm-label {
    padding: 0px 6px;
    background: #8b98a1;
    color: #fff;
    border-radius: 2px;
    font-size: 12px;
    font-weight: normal;
    line-height: 20px;
    text-align: center;
    min-width: 46px;
    display: inline-block;
  }

  .gm-sectionTitle {
    font-size: 1.1em;
    margin: 5px 0 10px;
  }

  .gm-visuallyhidden {
    clip: rect(1px, 1px, 1px, 1px);
    height: 1px;
    overflow: hidden;
    position: absolute !important;
    width: 1px;
  }
}
