#graphmilker {
  .gm-graphmilker {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 14px;
    line-height: 24px;
    min-height: 500px;
    width: 100%;
    height: 100%;
    text-align: left;
  }

  .gm-graphmilker__title {
    background-color: #8b98a1;
    color: #fff;
    flex-basis: auto;
    flex-grow: 0;
    font-size: 1.1em;
    margin: 0;
    padding: 10px 20px;
  }

  .gm-graphmilker__main {
    background-color: #E1E2E1;
    flex-basis: auto;
    flex-grow: 1;
    margin: 0;
    padding: 10px 20px 20px;
    position: relative;
  }
}
